import detailInfo from "../../components/detail/detail-info.vue";
import plComment from "../../components/ygc-comment/ygc-comment.vue";
import time from "../../common/time.js";
import commentList from "../../components/detail/comment-list.vue";
import userChatBottom from "../../components/user-chat/user-chat-bottom.vue";
import moreShare from "../../components/common/more-share.vue";
import {mapState, mapMutations} from "vuex"
export default {
	components:{
		detailInfo,
		commentList,
		userChatBottom,
		moreShare,
		plComment
	},
	data() {
		return {
			shareshow:false,
			currentComm:{},
			comment:{
				count:0,
				list:[]
			},
			detail:{},
			maskState:false
		}
	},
	onLoad(e) {
		try{
			let data = JSON.parse(uni.getStorageSync("topicDatail"))
			this.initdata(data);
			this.getcomment();
			if(this.userInfo.id){
				this.pushHistory(data)
			}
			
		}catch(e){
			
		}
	},
	// 监听导航右边按钮
	onNavigationBarButtonTap(e) {
		if(e.index==0){
			this.togle();
		}
	},
	computed:{
		...mapState(['userInfo'])
	},
	methods: {
		pushHistory(data){
			this.$http.post('topic/history',{
				cid:data.cid,
				tid:data.id,
				uid:this.userInfo.id
			})
		},
		togle(){
			this.shareshow=!this.shareshow
		},
		comSubimt(item){
			this.currentComm = item
			this.maskState = true
		},
		async comDelete(item){
			let data =await this.$http.delete("comment/"+item.id);
			await this.getcomment();
		},
		pubComment(text){
			if(text==""){
				return
			}
			this.$http.post('comment',{
				uid:this.userInfo.id,
				parentId:this.currentComm.id,
				tid:this.currentComm.tid,
				content: text
			}).then(()=>{
				this.getcomment()
				this.maskState = !this.maskState
			})
			
			
		},
		toggleState(){
			this.maskState = !this.maskState
		},
		submit(data){
			if(!this.userInfo.id){
				uni.showToast({
					title:"你还未登录!",
					icon:'none'
				})
				return
			}
			if(data==''){
				uni.showToast({
					title:"评论不能为空",
					icon:"none"
				})
				return ;
			}
			this.$http.post('comment',{
				uid:this.userInfo.id,
				parentId:0,
				tid:this.detail.id,
				content: data
			}).then(()=>{
				this.getcomment()
			})
		},
		goToUserInfo(item){
			uni.navigateTo({
				url:'../../pages/user-space/user-space?uid='+item.uid
			})
		},
		// 获取评论
		async getcomment(){
			let arr = await this.$http.get('comment/'+this.detail.id)
			this.comment.list=arr.items;
			this.comment.count = arr.total;
			this.detail.commentNum = arr.total
		},
		// 初始化数据
		initdata(obj){
			// 修改窗口标题
			uni.setNavigationBarTitle({ title: "详情"});
			this.detail = obj
			this.comment.count = obj.commentNum
		}
	}
}
